body.white{
    .skills-wrapper{
        background-color: transparent;
        box-shadow: 5px 5px 30px -15px black;

        .title{
            border-color: black;
        }
    }
}

.skills-wrapper{
    display: flex;
    flex-direction: column;
    height: 100%;
    
    background-color: #2c2c2c;
    box-shadow: 10px 0px 30px -10px black;
    
    border-radius: 50px;
    margin: 0 auto;

    transition: background-color .3s ease;
    
    .title{
        text-align: center;
        width: 100%;
        margin: 0 auto;
        
        border-bottom: 3px solid rgb(213, 213, 213);
        padding: 20px 0;
    }
}

.skills_inner{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 15px 20px;
    padding: 40px 30px 140px;
}

@media (min-width: 2000px){
    .skills-wrapper{
        font-size: 30px;
    }   
}

@media (max-width: 630px){
    .skills_inner{
        padding: 40px 10px 100px 20px;
    }
}